i.fab::before {
    font-size: 40px;  
}
i.far::before{
    font-size: 40px;
}
.list{
    display: flex;
    color:white;
    flex-direction: row;
    width:100%;
    align-items: center;
    justify-content: space-between;
}
@media only screen and (max-width:768px)  and (max-height:1024px){
    .list{
        display: flex;
        color:white;
        width: 100%;
        flex-direction: row;
        align-items: center;
        justify-content:space-between;
    }
}
@media only screen and (max-width: 767px){
    .list{
        display: flex;
        color:white;
        width: 100%;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    
    .listItem {
        display: inline-block;
        margin-left: -20px;
        margin-right: -20px;
        margin-top: 0px;
    }
    i.fab::before {
        font-size: 20px;  
    }
    i.far::before{
        font-size: 20px;
    }
} 
@media only screen and (max-width:500px){
    .list{
        display: flex;
        color:white;
        flex-direction: row;
        width: 100%;
        align-items: center;
    justify-content: center;
    }
    i.fab::before {
        font-size: 20px;  
    }
    i.far::before{
        font-size: 20px;
    }
}
@media only screen and (min-device-width:717px) and (max-device-height:512px)  {
    .list{
        display: flex;
        color:white;
        flex-direction: row;
        width: 100%;
        align-items: center;
    justify-content: center;
    }
}
@media only screen and (min-device-width: 1024px) and (max-device-height: 1366px) and (-webkit-min-device-pixel-ratio: 2)  and (orientation: portrait)  {
    .list{
        display: flex;
        color:white;
        flex-direction: row;
        width: 100%;
        align-items: center;
    justify-content: center;
    }
}