.resume-section ul.education {
    margin: 50px 0px;
    margin-left:30px;
    list-style: none;
    position: relative;
    left:10%;
    width: 50%;
}

.resume-section ul.education li{
    position: relative;
    margin-left: 10px;
    padding: 20px;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.resume-section ul.education li:not(:first-child){
    margin-top: 60px;
}

.resume-section ul.education li span.line-left{
    width: 2px;
    height: 100%;
    background-color: #e1e1e1;
    left: -10px;
    top: 0;
    position: absolute;
}

.resume-section ul.education li span.line-left:before,
.resume-section ul.education li span.line-left:after{
    content: "";
    width: 10px;
    height: 10px;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    border: 2px solid #e1e1e1;
    position: absolute;
    left: -4px;
    top: -10px;
}

.resume-section ul.education li span.line-left:after{
    top: 100%;
}

.resume-section .education .content {
    margin-left: 10px;
}

.resume-section .education .content h4{
    font-size: 18px;
    font-weight: 700;
    letter-spacing: .5px;
    line-height: 26px;
    margin: 0;
    text-transform: uppercase;
}

.resume-section .education .content h5{
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 12px;
    font-family: "Roboto", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
}

.resume-section .education .content p.info{
    font-size: 16px;
    margin-bottom: 0px;
}

.resume-section .education span.year{
    height: 100%;
    left: -10px;
    top: 0;
    position: absolute;
}

.resume-section .education span.year span{
    font-size: 16px;
    position: absolute;
    -webkit-transform: translateX(-120%);
    -ms-transform: translateX(-120%);
    transform: translateX(-120%);
    color: #ffffff;
}

.resume-section .education span.year span.to{
    top: -18px;
}

.resume-section .education span.year span.from{
    bottom: -18px;
}
.list-heading{
    margin-top: 25px;
    margin-left: 20px;
}
.list-inline.list-icons{
    margin-top: -5px;
    margin-left: 25px;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
    margin-bottom: -25px;
}
.list-inline.list-icons{font-size:3rem} 
.list-inline.list-icons  li a{
    color: white;
    font-size: 40px;
    margin-right: -40px;
}
